@import '../../_utils/font.module.scss';
@import '../../_utils/variables.module.scss';

@keyframes button {
  0% {
    transform: scale(100%);
  }

  50% {
    transform: scale(130%);
  }

  100% {
    transform: scale(100%);
  }
}

.button {
  background-image: url($button-start-regular-url);
  background-repeat: no-repeat;
  background-size: contain;
  width: 150px;
  height: 50px;
  cursor: pointer;
  animation: button 2s ease-in-out infinite;

  &:hover {
    animation-play-state: paused;
    background-image: url($button-start-hover-url);
  }
}

.GameOver {
  position: absolute;
  left: 43%;
  top: 45%;
  z-index: 100;

  &:hover {
    animation-play-state: paused;
    background-image: url($button-start-hover-url);
  }
}

.infoButton {
  background-image: url($button-info-url);
  background-repeat: no-repeat;
  background-size: contain;
  width: 150px;
  height: 50px;
  cursor: pointer;

  &:hover {
    background-image: url($button-info-hover-url);
  }
}

.hidden {
  display: none;
}

.container {
  display: flex;
  flex-direction: column;
  row-gap: 110px;
  box-sizing: border-box;
  width: 132px;
}
