@import '../../_utils/font.module.scss';
@import '../../_utils/variables.module.scss';

@keyframes meteors {
  0% {
    left: 0px;
  }
  100% {
    left: -4800px;
  }
}

.playground {
  position: absolute;
  z-index: 800000;
  width: 1200px;
  left: 0;
  height: 600px;
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 6000px;
  height: 100%;
}

.meteor {
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
}

.spaceship {
  width: 150px;
  height: 60px;
  position: absolute;
  background-image: url($spaceship-url);
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 500000;
}

.rescued {
  display: none;
}

@keyframes explode {
  0% {
    transform: scale(0.1);
    opacity: 0;
  }

  5% {
    opacity: 1;
  }

  100% {
    transform: scale(1);
  }
}

.explosion {
  width: 100px;
  height: 100px;
  position: absolute;
  z-index: 501000;
  background-image: url($explosion-url);
  background-repeat: no-repeat;
  background-size: contain;
  animation: explode ease-in-out 0.3s;
}
